import React, { Component } from 'react';
import compass_rose from './media/compass_rose.svg';
import ReactSVG from 'react-svg'
import "foundation-sites";
import $ from 'jquery';


class SteeringCompass extends Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            course: [],
            set_cts: 0,
        };

    }

    getCompass(){
        fetch("http://localhost:8079/boat_data")
            .then(response => response.json())
            .then(
                (result) => {
                    this.setState({
                        isLoaded: true,
                        course: result['course']
                    });
                },
                // Note: it's important to handle errors here
                // instead of a catch() block so that we don't swallow
                // exceptions from actual bugs in components.
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    });

                }
            );
    }

    postCts(){
        fetch('http://localhost:8079/boat_data', {
            method: 'post',
            headers: {
                'Accept': 'application/json, text/plain, */*',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                "course": {"cts": this.state.set_cts}
                })
        }).then(res=>res.json())
            .then(res => {
                    this.setState({
                        isLoaded: true,
                        course: res['course']
                    });
                },
            );

    }

    updateDisplay(){
        console.log(this.state);
        let cts = this.state.course['cts'];
        let heading = this.state.course['heading'];
        $('#headingDisplay').val(heading);
        const heading_svg = document.getElementById('compass_heading');
        console.log(heading_svg);
        heading_svg.setAttribute("transform", "rotate(" + heading + ")");
        heading_svg.setAttribute("transform-origin", "50% 50%");
        const heading_course = document.getElementById('compass_course');
        console.log(heading_course);
        heading_course.setAttribute("transform", "rotate("+ cts + ")");
        heading_course.setAttribute("transform-origin", "50% 50%");
        const heading_rose = document.getElementById('compass_rose');
        console.log(heading_rose);
        heading_rose.setAttribute("transform", "rotate(0)");
        heading_rose.setAttribute("transform-origin", "50% 50%");
    }

    componentDidMount() {
        const heading_id = document.getElementById('svg1901');
        console.log(heading_id);
        //setInterval(this.updateCompass, 3000);
        setInterval(() => {
            this.getCompass();
            this.updateDisplay();
        }, 1000);
    }

    componentWillUnmount() {

    }

    render() {
    return (
        <div  className="grid-x grid-padding-x">
            <div  className="cell small-1" />
            <div className="cell small-5">
                <div  className="App-Compass">
                    <ReactSVG src={compass_rose} />
                </div>

                <div className="grid-x grid-padding-x">
                    <div className="medium-2 small-2 cell" />
                    <div className="medium-8 small-8 cell">
                        <div className="grid-x">
                            <label>Heading</label>
                            <div className="input-group">
                                <input id='headingDisplay' type="text" placeholder="heading" readOnly='True'
                                       className="input-group-field"/>
                                <span className="input-group-label">M deg</span>
                            </div>
                        </div>
                    </div>
                    <div className="medium-2 small-2 cell" />
                </div>

            </div>
            <div  className="cell small-1"/>
            <div  className="cell small-4">
                <h4>Course to Steer</h4>
                <div className="grid-x grid-margin-x">
                    <div className="cell small-8">
                        <div className="slider" data-slider data-initial-start="0" data-end="360">
                            <span className="slider-handle" data-slider-handle role="slider"
                                tabIndex="1" aria-controls="sliderOutputCourse" />
                            <span className="slider-fill" data-slider-fill />
                         </div>
                    </div>
                     <div className="cell small-4">
                        <input type="number" id="sliderOutputCourse" />
                     </div>
                </div>
                <a href="#" className="small button" onClick={
                        ()=>{
                            this.state.set_cts = parseInt($("#sliderOutputCourse").val(), 10);
                            this.postCts();
                            this.updateDisplay();
                        }
                    }>Set Course</a>
            </div>
            <div  className="cell small-1"/>
        </div>

    );
  }
}

export default SteeringCompass;
